html {
  font-family: 'Roboto Condensed', sans-serif;
}

h1 {
  font-size: large;
}

h2 {
  font-size: larger;
}

body {
  background-color: #de000009;
}


.container {
  
  width: auto;
  display: flex;
  justify-content: space-evenly;
}

.wrapper {
  margin-top: 7%;
  background-color: #b41b1b3b;
  border: 5px groove #DE0000;
  border-radius: 40px;
  text-align: center;
  width: 400px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  
}


.session {
  font-size: 30px;
}

.bottomSettings{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.buttons {
  display: flex;
  flex-direction: row;
}

.lengthSettings{
  font-size: 20px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 20px;
  
}

.lengthBox {
  margin: 25px;
  padding: 30px;
  border: 4px dashed rgba(222,0,0,0.62);
border-radius: 40px;
}

.session {
  margin-bottom: 20px;
}